import React from "react";
import {
  Paper,
  Grid,
  makeStyles,
  List,
  ListItem,
  ListItemIcon,
  ListItemText,
} from "@material-ui/core";
import StarsIcon from "@material-ui/icons/Stars";

const useStyles = makeStyles((theme) => ({
  cardWrapper: {
    width: "100%",
    margin: "0px",
    display: "flex",
    justifyContent: "center",
    marginTop: "-65px",
  },
  paper: {
    margin: "0px 20px",
    [theme.breakpoints.between("sm","md")]: {
      margin:0,
    },
    width: "280px",
    display: "flex",
    flexDirection: "column",
    textAlign: "center",
    borderRadius: 15,
    height: "auto",
  },
  cardPosition: {
    display: "flex",
    justifyContent: "center",
  },
  listItem: {
    alignItems:"flex-start",
    padding: "0px 20px",
    [theme.breakpoints.down("sm")]: {
      padding: "0px 10px",
    },
    [theme.breakpoints.down("xs")]: {
      padding: "0px 20px",
    },
  },
  listIcon: {
    color: "#D64539",
  },
}));

const ProductCards = (props) => {
  const { cardWrapper, paper, cardPosition, listItem, listIcon } = useStyles();
  const { content } = props;
  return (
    <>
      <Grid container spacing={4} className={cardWrapper}>
        {content.map((product, index) => (
          <Grid item sm={4} xs={12} className={cardPosition}>
            <Paper elevation={4} className={paper}>
              <h3>{product.title}</h3>
              <List>
                {product.list.map((subProducts, index) => (
                  <ListItem className={listItem}>
                    <ListItemIcon style={{ minWidth: 30 }}>
                      <StarsIcon className={listIcon} />
                    </ListItemIcon>
                    <ListItemText primary={subProducts} style={{marginTop:"0px",marginBottom:6}}/>
                  </ListItem>
                ))}
              </List>
            </Paper>
          </Grid>
        ))}
      </Grid>
    </>
  );
};

export default ProductCards;
