import {
	AppBar,
	Button,
	Grid,
	makeStyles,
	Tab,
	Tabs,
	Toolbar,
	useMediaQuery,
	Paper,
} from "@material-ui/core";
import ClickAwayListener from "@material-ui/core/ClickAwayListener";
import React from "react";
import { Link, withRouter } from "react-router-dom";
import { NavbarLinks, Products } from "../content/Home";
import MainContainer from "./mainContainer";
import MobileMenu from "./mobileMenu";
import ProductsBox from "./productsBox";
const useStyles = makeStyles((theme) => ({
	navbar: {
		backgroundColor: theme.palette.background.default,
		boxShadow: ` 7px 3px 10px #00000029`,
		height: 90,
		display: "flex",
		justifyContent: "center",
		alignItems: "center",
		[theme.breakpoints.down("xs")]: {
			height: 88,
		},
	},
	title: {
		position: "relative",
		display: "flex",
		justifyContent: "center",
		alignItems: "center",
	},
	navbarStyle: {
		width: "100%",
		[theme.breakpoints.down("xs")]: {
			padding: 0,
		},
	},
	imageLogoContainer: {
		display: "flex",
		alignItems: "center",
		width: 122,
		height: "100%",
		[theme.breakpoints.down("xs")]: {
			width: 74,
		},
	},
	imageLogo: {
		width: "100%",
	},
	navbarMenu: {
		padding: theme.spacing(1),
	},
	links: {
		"@media (max-width:1024px)": {
			fontSize: 17,
			minWidth: "unset",
		},
		fontSize: 16,
		marginLeft: theme.spacing(1),
		minWidth: 90,
		"& > span": {
			color: theme.palette.text.primary,
			fontWeight: theme.typography.semiBold,
		},
	},
	contactBtn: {
		"@media (max-width:1024px)": {
			fontSize: "16px",
			minWidth: "125px",
			minHeight: "35px",
		},
		marginLeft: theme.spacing(2),
		minWidth: 90,
		minHeight: 40,
		color: "#FFFFFF",
		boxShadow: "none",
		fontSize: 16,
		fontWeight: theme.typography.fontWeightMedium,
		"&:hover": {
			backgroundColor: theme.palette.primary.main,
			boxShadow: "none",
		},
	},
	link: {
		textDecoration: "none",
		color: "#FFFFFF",
	},
}));

const Navbar = (props) => {
	const {
		navbar,
		title,
		links,
		contactBtn,
		navbarMenu,
		navbarStyle,
		imageLogo,
		imageLogoContainer,
		subProductsContainer,
		link,
	} = useStyles();
	const [value, setValue] = React.useState(0);

	const handleChange = (event, newValue) => {
		setValue(newValue);
		if (newValue === 0) props.history.push("/");
		if (newValue === 2) props.history.push("/team");
		if (newValue === 3) props.history.push("/testimonial");
	};

	const [show, setShow] = React.useState(false);

	const handleClickAway = () => {
		setShow(false);
	};

	const mobileMatcher = useMediaQuery((theme) => theme.breakpoints.up("md"));
	const ContactUsBtn = (
		<a
			target="_blank"
			rel="noopener noreferrer"
			className={link}
			href="https://form.jotform.me/91042672900451"
		>
			<Button variant="contained" className={contactBtn} color="primary">
				Contact Us
			</Button>
		</a>
	);

	const menu = mobileMatcher ? (
		<div className={title}>
			<div className={navbarMenu}>
				<ClickAwayListener onClickAway={handleClickAway}>
					<Tabs
						value={value}
						onChange={handleChange}
						indicatorColor="primary"
						textColor="primary"
						centered
					>
						{NavbarLinks.map((link) => {
							return (
								<Tab
									disableRipple
									className={links}
									key={link.id}
									label={link.name}
									onClick={
										link.name === "Products"
											? () => {
													setShow(!show);
											  }
											: handleClickAway
									}
								/>
							);
						})}
					</Tabs>
				</ClickAwayListener>
			</div>
			<div className={navbarMenu}>{ContactUsBtn}</div>
		</div>
	) : (
		<div>
			<MobileMenu items={NavbarLinks} unique={ContactUsBtn} />
		</div>
	);

	return (
		<AppBar position="static" className={navbar}>
			<Toolbar className={navbarStyle}>
				<MainContainer>
					<Grid container>
						<Grid item xs={6} lg={3} md={3}>
							<Link to="/" className={imageLogoContainer}>
								<img
									className={imageLogo}
									src={require("../assets/Home/casa-retailai-dark.png")}
									alt="Casa-RetailAI-Dark"
								/>
							</Link>
						</Grid>
						<Grid item xs={6} lg={9} md={9}>
							{menu}
							{show && (
								<Paper
									elevation={3}
									className={subProductsContainer}
									style={{
										position: "absolute",
										background: "white",
										padding: "10px",
										width: "45em",
										zIndex: 1,
									}}
								>
									<ProductsBox SubProductLinks={Products} />
								</Paper>
							)}
						</Grid>
					</Grid>
				</MainContainer>
			</Toolbar>
		</AppBar>
	);
};

export default withRouter(Navbar);
