import { makeStyles, useMediaQuery } from "@material-ui/core";
import React from "react";
import Caption from "./caption";
import SliderCarousel from "./carousel";
import { BenifitedLogos } from "../content/Home";
import _ from "lodash";
const useStyles = makeStyles((theme) => ({
	benifit: {
		background:
			"linear-gradient(0deg, rgb(250, 210, 204) 0%, rgb(255, 255, 255) 74%)",
		padding: theme.spacing(2),
		minHeight: 147,
		width: "100%",
	},
	benifitCaption: {
		fontWeight: theme.typography.semiBold,
	},
	benifitedLogoContainer: {
		display: "flex",
		justifyContent: "center",
		alignItems: "center",
		[theme.breakpoints.down("sm")]: {
			overflow: "hidden",
		},
	},
	logoList: {
		display: "grid",
		alignItems: "center",
		gridTemplateColumns: "repeat(7,1fr)",
		[theme.breakpoints.down("sm")]: {
			gridTemplateColumns: "repeat(2,1fr)",
			width: "100%",
			padding: 0,
		},
		width: "75%",
		listStyleType: "none",
	},
	benifitedLogo: {
		width: 95,
		height: 21,
		[theme.breakpoints.down("sm")]: {
			width: 123,
			height: 28,
		},
	},
	logoItem: {
		textAlign: "center",
	},
	arrowStyles: {
		color: "#00000081",
		position: "absolute",
		zIndex: 2,
		top: "30%",
		cursor: "pointer",
		backgroundColor: "transparent",
	},
	benifitedContainer: {
		display: "flex",
		justifyContent: "center",
		alignItems: "center",
		width: "100%",
		height: "100%",
	},
	imageContainer: {
		display: "flex",
		justifyContent: "space-evenly",
		alignItems: "center",
		padding: theme.spacing(3),
	},
	image: {
		height: 76,
		[theme.breakpoints.down("sm")]: {
			width: "unset !important",
			height: 50,
		},
	},
	partnerContainer: {
		display: "flex",
		justifyContent: "center",
		alignItems: "center",
		width: "100%",
		height: "100%",
	},
}));

const CustomerBenifitHistory = (props) => {
	const {
		benifit,
		benifitedLogoContainer,
		arrowStyles,
		partnerContainer,
		imageContainer,
		image,
	} = useStyles();
	const mobileMatcher = useMediaQuery((theme) =>
		theme.breakpoints.down("md")
	);
	return (
		<div className={benifit}>
			<Caption align="center">Brands that Love Us</Caption>
			<br />
			<div className={benifitedLogoContainer}>
				<SliderCarousel autoPlay arrowStyles={arrowStyles}>
					{_.chunk(BenifitedLogos, mobileMatcher ? 2 : 6).map(
						(partnerWrapper) => (
							<div className={partnerContainer}>
								{partnerWrapper.map((partner) => (
									<div className={imageContainer}>
										<img
											className={image}
											key={partner.id}
											src={partner.source}
											alt={`Partner ${partner.id}`}
										/>
									</div>
								))}
							</div>
						)
					)}
				</SliderCarousel>
			</div>
		</div>
	);
};

export default CustomerBenifitHistory;
