import React, { useState } from "react";
import { makeStyles, withStyles } from "@material-ui/core/styles";
import MuiAccordion from "@material-ui/core/Accordion";
import MuiAccordionSummary from "@material-ui/core/AccordionSummary";
import MuiAccordionDetails from "@material-ui/core/AccordionDetails";
import Typography from "@material-ui/core/Typography";
import { Container } from "@material-ui/core";
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";
import ListOfLinks from "./listOfLinks";
const Accordion = withStyles((theme) => ({
	root: {
		border: "1px solid rgba(0, 0, 0, .125)",
		boxShadow: "none",
		backgroundColor: "transparent",
		color: "inherit",
		"&:not(:last-child)": {
			borderBottom: 0,
		},
		"&:before": {
			display: "none",
		},
		"&$expanded": {
			margin: "auto",
		},
	},
	expanded: {},
}))(MuiAccordion);

const AccordionSummary = withStyles((theme) => ({
	root: {
		borderBottom: "3px solid rgba(0, 0, 0, .125)",
		backgroundColor: "#707070",
		marginBottom: -1,
		fontWeight: theme.typography.semiBold,
		minHeight: 56,
		"&$expanded": {
			minHeight: 56,
		},
	},
	content: {
		"&$expanded": {
			margin: "12px 0",
		},
	},
	expanded: {},
}))(MuiAccordionSummary);

const AccordionDetails = withStyles((theme) => ({
	root: {
		padding: theme.spacing(2),
	},
}))(MuiAccordionDetails);
const useStyles = makeStyles((theme) => ({
	icon: {
		color: "#FFFFFF",
	},
	link: {
		color: "inherit",
		textDecoration: "none",
	},
}));
export default function SliderAccordion(props) {
	const { icon } = useStyles();
	const { details } = props;
	const [expanded, setExpanded] = useState(null);

	const handleChange = (panel) => (event, newExpanded) => {
		setExpanded(newExpanded ? panel : false);
	};

	return (
		<Container disableGutters>
			{details.map((detail) => (
				<Accordion
					expanded={expanded === detail.id}
					square
					onChange={handleChange(detail.id)}
				>
					<AccordionSummary
						expandIcon={<ExpandMoreIcon className={icon} />}
						aria-controls={`panel-content-${detail.id}`}
						id={`panel-header-${detail.id}`}
					>
						<Typography>{detail.summary}</Typography>
					</AccordionSummary>
					<AccordionDetails key={detail.id}>
						<ListOfLinks details={detail.details} />
					</AccordionDetails>
				</Accordion>
			))}
		</Container>
	);
}
