import { Grid, makeStyles, Typography } from "@material-ui/core";
import React from "react";
import { Link } from "react-router-dom";

const useStyles = makeStyles((theme) => ({
	linkTabs: {
		display: "flex",
		// alignItems: "center",
		color: "inherit",
		textDecoration: "none",
		padding: theme.spacing(1.5),
		paddingLeft: 0,
		minHeight: 84,
	},
	headerStyle: {
		fontWeight: theme.typography.semiBold,
		fontSize: 14,
		lineHeight: 1.5,
		[theme.breakpoints.down("sm")]: {
			fontSize: 12,
		},
	},
	discriptionStyle: {
		fontSize: 11,
	},
	linkContainer: {
		padding: theme.spacing(1),
	},
	image: {
		width: "75% !important",
		[theme.breakpoints.down("sm")]: {
			width: ` 37px !important`,
		},
	},
}));

const SubProductLink = (props) => {
	const { imgSrc, discription, header, path } = props;
	const {
		linkTabs,
		image,
		headerStyle,
		discriptionStyle,
	} = useStyles();
	return (
		<Link to={path} className={linkTabs}>
			<Grid container>
				<Grid item xs={3}>
					<img className={image} src={imgSrc} alt="Subproduct" />
				</Grid>
				<Grid item xs={9}>
					<Typography className={headerStyle}>{header}</Typography>
					<Typography className={discriptionStyle}>
						{discription}
					</Typography>
				</Grid>
			</Grid>
		</Link>
	);
};

export default SubProductLink;
