import { Container, makeStyles } from "@material-ui/core";
import React, {  useState } from "react";
import { Activities, SubProductTitle } from "../../../content/Home";
import { CustomTab, CustomTabs } from "../../../components/customTabs";
import TabContent from "./TabContent";
import Caption from "../../../components/caption";
import SliderCarousel from "../../../components/carousel";
import { SubProductsContent } from "../../../content/Home";

const useStyles = makeStyles((theme) => ({
	arrowStyles: {
		color: "#00000081",
		position: "absolute",
		zIndex: 2,
		cursor: "pointer",
		backgroundColor: "transparent",
		display: "none",
		top: "calc(25% - 15px)",
		[theme.breakpoints.down("sm")]: {
			display: "block",
			top: "calc(18% - 15px)",
		},
	},
	contentContainer: {
		[theme.breakpoints.down("sm")]: {
			padding: 0,
		},
	},
	tabs: {
		display: "flex",
		justifyContent: "center",
		alignItems: "center",
		marginBottom: theme.spacing(3),
	},
	carousel: {
		backgroundColor: "white",
	},
	productTabs: {
		[theme.breakpoints.down("sm")]: {
			fontSize: 18,
			minWidth: 122,
			marginRight: theme.spacing(2),
		},
	},
}));
const SubProducts = (props) => {
	const {
		contentContainer,
		tabs,
		carousel,
		productTabs,
		arrowStyles,
	} = useStyles();
	const [currentSlide, setCurrentSlide] = useState(0);

	const handleChange = (event, newValue) => {
		setCurrentSlide(newValue);
	};

	const updateCurrentSlide = (index) => {
		if (currentSlide !== index) {
			setCurrentSlide(index);
		}
	};
	return (
		<div>
			<Container className={contentContainer}>
				<div>
					<Caption>{SubProductTitle}</Caption>
				</div>
				<div className="addHeight"></div>
				<div className={tabs}>
					<CustomTabs
						variant="scrollable"
						value={currentSlide}
						onChange={handleChange}
						aria-label="Custom Tab"
					>
						{Activities.map((activity) => (
							<CustomTab
								id={`SubProduct Tab ${activity.id}`}
								aria-controls={`tabpanel-${activity.id}`}
								key={activity.id}
								label={activity.name}
								className={productTabs}
							/>
						))}
					</CustomTabs>
				</div>

				<SliderCarousel
					currentSlide={currentSlide}
					onChange={updateCurrentSlide}
					arrowStyles={arrowStyles}
				>
					{SubProductsContent.map((itemProps) => {
						return (
							<div key={itemProps.id} className={carousel}>
								<TabContent {...itemProps} />
							</div>
						);
					})}
				</SliderCarousel>
			</Container>
		</div>
	);
};

export default SubProducts;
