import { Grid, makeStyles, Typography } from "@material-ui/core";
import React from "react";
import { StatIcons } from "../../content/Home";

const useStyles = makeStyles((theme) => ({
	contentContainer: {
		display: "flex",
		minHeight: 250,
		alignItems: "center",
		margin: `${theme.spacing(3)}px 0`,
	},
	imageContainer: {
		display: "flex",
		justifyContent: "center",
		alignItems: "center",
		height: "100%",
	},
	image: {
		width: 115,
		[theme.breakpoints.down("sm")]: {
			width: 70,
		},
	},
	header: {
		fontSize: 24,
		fontWeight: theme.typography.fontWeightBold,
		[theme.breakpoints.down("sm")]: {
			fontSize: 15,
		},
	},
	discription: {
		fontSize: 20,
		fontWeight: theme.typography.semiBold,
		[theme.breakpoints.down("sm")]: {
			fontSize: 14,
		},
	},
}));
const BriefStats = (props) => {
	const {
		contentContainer,
		image,
		imageContainer,
		header,
		discription,
	} = useStyles();

	return (
		<div className={contentContainer}>
			<Grid container>
				{StatIcons.map((icon) => (
					<Grid item key={icon.id} xs={6} lg={3}>
						<div>
							<div className={imageContainer}>
								<img
									className={image}
									src={icon.source}
									alt={`Award ${icon.id}`}
								/>
							</div>
							<Typography align="center" className={header}>
								{icon.header}
							</Typography>
							<Typography align="center" className={discription}>
								{icon.discription}
							</Typography>
						</div>
					</Grid>
				))}
			</Grid>
		</div>
	);
};

export default BriefStats;
