import React from "react";
import { Container, Grid, makeStyles } from "@material-ui/core";

const useStyles = makeStyles((theme) => ({
  container: {
    height: 350,
    display: "flex",
    alignItems: "center",
  },
  contentWrapper: {
    display: "flex",
    justifyContent: "center",
  },
  content: {
    width: "70%",
    [theme.breakpoints.down("xs")]: {
      width: "100%",
    },
    [theme.breakpoints.down("sm")]: {
      width: "90%",
    },
  },
  title: {
    fontSize: 32,
    fontWeight: "bolder",
    [theme.breakpoints.down("sm")]: {
      fontSize: 18,
      display: "flex",
      justifyContent: "center",
    },
    [theme.breakpoints.down("md")]: {
      justifyContent: "flex-start",
    },
  },
  productBanner: {
    width: "100%",
    height: "280px",
    objectFit: "initial",
    [theme.breakpoints.between("xs", "sm")]: {
      height: "200px",
      objectFit: "contain",
    },
  },
  tabContent: {
    height: 350,
    display: "flex",
    alignItems: "center",
    [theme.breakpoints.between("xs", "sm")]: {
      height:450
    },
  },
}));

export default function TabContents(props) {
  const { productInfo } = props;
  const {
    contentWrapper,
    productBanner,
    content,
    title,
    tabContent,
  } = useStyles();

  return (
    <Container className={tabContent}>
      {/* <div className={wrapper}> */}
      <Grid container>
        <Grid item sm={4}>
          <img
            src={productInfo.source}
            alt="ProductInfo"
            className={productBanner}
          />
        </Grid>
        <Grid item sm={8}>
          <div className={contentWrapper}>
            <div className={content}>
              <div className={title}>{productInfo.content.title}</div>
              <p>{productInfo.content.details}</p>
              <p>{productInfo.content.conclusion}</p>
            </div>
          </div>
        </Grid>
      </Grid>
      {/* </div> */}
    </Container>
  );
}
