import React from "react";
import { makeStyles, Grid, useMediaQuery } from "@material-ui/core";
import { craftsmanList } from "../../content/Team";
import SliderCarousel from "../../components/carousel";

const useStyles = makeStyles((theme) => ({
	root: {
		minHeight: 955,
		// background: "#F8F9FB",
		display: "flex",
		justifyContent: "center",
		alignItems: "center",
		[theme.breakpoints.between("xs", "sm")]: {
			minHeight: "auto",
		},
	},
	wrapper: {
		width: "100%",
		height: "100%",
		display: "flex",
		flexDirection: "column",
		alignItems: "center",
	},
	title: {
		margin: "40px 0",
	},
	craftsman: {
		width: "100%",
		display: "flex",
		justifyContent: "center",
		textAlign: "center",
	},
	craftsmanName: {
		marginTop: 5,
		fontWeight: theme.typography.semiBold,
	},
	craftsmanPosition: {
		padding: 10,
	},
	cardPosition: {
		height: 260,
	},
	profile: {
		display: "flex",
		justifyContent: "center",
		height: 100,
		width: "100%",
		borderRadius: 50,
	},
	socials: {
		marginRight: 15,
	},
	remarks: {
		width: "80%",
		height: 205,
		margin: "60px 0px",
		boxShadow: "20px 32px 0px 0px rgba(240,118,99,1)",
		[theme.breakpoints.between("xs", "sm")]: {
			width: "94%",
			height: 165,
			boxShadow: "7px 25px 0px 0px rgba(240,118,99,1)",
		},
	},
	comments: {
		width: "100%",
		height: "100%",
		boxShadow: "1px 0px 6px 2px rgba(0, 0, 0, 0.25)",
		padding: 30,
		[theme.breakpoints.between("xs", "sm")]: {
			display: "flex",
			flexDirection: "column",
			justifyContent: "center",
			padding: "20px 15px",
		},
	},
	userInfo: {
		width: "100%",
		display: "flex",
		justifyContent: "flex-end",
		[theme.breakpoints.between("xs", "sm")]: {
			marginTop: "15px",
		},
	},
	name: {
		margin: 0,
		[theme.breakpoints.between("xs", "sm")]: {
			fontSize: 16,
		},
	},
	caption: {
		fontSize: 29,
		color: "#F07663",
		lineHeight: "45px",
		[theme.breakpoints.between("xs", "sm")]: {
			fontSize: 14,
			lineHeight: "20px",
		},
	},
	role: {
		[theme.breakpoints.between("xs", "sm")]: {
			fontSize: 11,
		},
	},
	arrowStyles: {
		color: "#00000081",
		position: "absolute",
		zIndex: 2,
		cursor: "pointer",
		backgroundColor: "transparent",
		display: "none",
		top: "calc(25% - 15px)",
		[theme.breakpoints.down("sm")]: {
			display: "block",
			top: "calc(18% - 15px)",
		},
	},
	carousel: {
		backgroundColor: "white",
		height: 300,
	},
}));

const CraftsMen = () => {
	const {
		root,
		wrapper,
		title,
		craftsman,
		craftsmanName,
		craftsmanPosition,
		cardPosition,
		profile,
		remarks,
		comments,
		userInfo,
		name,
		role,
		caption,
		arrowStyles,
		carousel,
	} = useStyles();
	const matches = useMediaQuery((theme) => theme.breakpoints.down("xs"));

	const card = (craftman) => {
		return (
			<Grid Item sm={3} xs={12} className={cardPosition}>
				<div className={profile}>
					<img
						src={craftman.source}
						alt={craftman.name}
						style={{ width: 100 }}
					/>
				</div>
				<div className={craftsmanName}>{craftman.name}</div>
				<div className={craftsmanPosition}>{craftman.position}</div>
			</Grid>
		);
	};

	return (
		<div className={root}>
			<div className={wrapper}>
				<h1 className={title}>Our Craftsmen</h1>
				<div className={craftsman}>
					<div
						style={
							matches ? { display: "none" } : { display: "block" }
						}
					>
						<Grid container>
							{craftsmanList &&
								craftsmanList.map((craftman) => card(craftman))}
						</Grid>
					</div>
				</div>
				<div
					style={
						matches
							? {
									display: "block",
									width: "100%",
									background: "rgb(248, 249, 251)",
							  }
							: { display: "none" }
					}
				>
					<SliderCarousel arrowStyles={arrowStyles}>
						{craftsmanList.map((craftman, index) => {
							return (
								<div key={index} className={carousel}>
									{card(craftman)}
								</div>
							);
						})}
					</SliderCarousel>
				</div>

				<div className={remarks}>
					<div className={comments}>
						<em className={caption}>
							"Its the effort of our entire team that enabled us
							to create a product that solves a complex problem"
						</em>
						<div className={userInfo}>
							<div>
								<h3 className={name}>Gowthamraj</h3>
								<span className={role}>
									Software Engineer,CASA
								</span>
							</div>
						</div>
					</div>
				</div>
				<br />
				<br />
			</div>
		</div>
	);
};

export default CraftsMen;
