import React from "react";
import Intelliclose from "./intelliclose/index";
import CampaignDelight from "./campaignDelight/index";
import Customer360 from "./customer360/index";
import CustomerCube from "./customerCube/index";
import Offers4u from "./offers4U/index";
import RetenSee from "./retenSee/index";
import OmniCommerce from "./omniCommerce/index";
import FlipSell from "./flipSell/index";
import FieldSalesAssist from "./fieldSalesAssist/index";

const index = (props) => {
  const products = {
    intelliclose: {
      component: <Intelliclose />,
    },
    campaignDelight: {
      component: <CampaignDelight />,
    },
    customer360: {
      component: <Customer360 />,
    },
    customerCube: {
      component: <CustomerCube />,
    },
    offers4u: {
      component: <Offers4u />,
    },
    retenSee: {
      component: <RetenSee />,
    },
    omniCommerce: {
      component: <OmniCommerce />,
    },
    flipSell: {
      component: <FlipSell />,
    },
    fieldSalesAssist: {
      component: <FieldSalesAssist />,
    },
  };
  return <>{products[props.match.params.name].component}</>;
};

export default index;
