import { makeStyles, useMediaQuery } from "@material-ui/core";
import React from "react";
import Caption from "../../components/caption";
import SliderCarousel from "../../components/carousel";
import { PartnerLogos } from "../../content/Home";
import _ from "lodash";

const useStyles = makeStyles((theme) => ({
	arrowStyles: {
		color: "#00000081",
		position: "absolute",
		zIndex: 2,
		top: "43%",
		cursor: "pointer",
		backgroundColor: "transparent",
	},
	partnerContainer: {
		display: "flex",
		justifyContent: "center",
		alignItems: "center",
		width: "100%",
		height: "100%",
	},
	imageContainer: {
		display: "flex",
		justifyContent: "space-evenly",
		alignItems: "center",
		padding: theme.spacing(3),
	},
	image: {
		height: 76,
		[theme.breakpoints.down("sm")]: {
			height: 50,
		},
	},
}));

const Partners = (props) => {
	const {
		partnerContainer,
		imageContainer,
		image,
		arrowStyles,
	} = useStyles();
	const mobileMatcher = useMediaQuery((theme) =>
		theme.breakpoints.down("sm")
	);
	return (
		<div>
			<Caption>Our Proud Partners</Caption>
			<SliderCarousel autoPlay arrowStyles={arrowStyles}>
				{_.chunk(PartnerLogos, mobileMatcher ? 2 : 6).map(
					(partnerWrapper) => (
						<div className={partnerContainer}>
							{partnerWrapper.map((partner) => (
								<div className={imageContainer}>
									<img
										className={image}
										key={partner.id}
										src={partner.source}
										alt={`Partner ${partner.id}`}
									/>
								</div>
							))}
						</div>
					)
				)}
			</SliderCarousel>
		</div>
	);
};

export default Partners;
