import { Grid, makeStyles } from "@material-ui/core";
import React from "react";
import Caption from "../../components/caption";
import { AwardsList } from "../../content/Home";
const useStyles = makeStyles((theme) => ({
	contentContainer: {
		display: "flex",
		alignItems: "center",
		// color: "#1D2B37",
		backgroundColor: "#F8F8F8",
		minHeight: 350,
		margin: `${theme.spacing(3)}px 0`,
	},
	awardsContainer: {
		[theme.breakpoints.down("sm")]: {
			display: "flex",
			alignItems: "center",
			minHeight: 150,
		},
	},
	imageContainer: {
		display: "flex",
		justifyContent: "center",
		alignItems: "center",
		height: "100%",
		margin: theme.spacing(2),
	},
	image: {
		width: "60%",
		[theme.breakpoints.down("sm")]: {
			width: 118,
		},
	},
}));
const Awards = (props) => {
	const {
		contentContainer,
		image,
		imageContainer,
		awardsContainer,
	} = useStyles();
	return (
		<div className={contentContainer}>
			<div>
				<Caption>Our Awards</Caption>
				<div className={awardsContainer}>
					<Grid container>
						{AwardsList.map((award) => (
							<Grid item key={award.id} xs={6} lg={3}>
								<div className={imageContainer}>
									<img
										className={image}
										src={award.source}
										alt={`Award ${award.id}`}
									/>
								</div>
							</Grid>
						))}
					</Grid>
				</div>
			</div>
		</div>
	);
};

export default Awards;
