import React from "react";
import { makeStyles} from "@material-ui/core/styles";
import { ProductTabs, ProductCustomTab } from "./productTab/productTab";
import TabContents from "./productTab/TabContents";
import TabPanel from "./productTab/TabPanel";

const useStyles = makeStyles((theme) => ({
  root: {
    backgroundColor: theme.palette.background.paper,
    width: "93%",
    [theme.breakpoints.between("xs","sm")]: {
      width: "100%",
      // height:600
    },
    height: "auto",
    margin: "auto",
    border: "2px solid #70707075",
    borderRadius: "40px 40px 0px 0",
    marginTop:30
  },
  tabs: {
    borderBottom: "2px solid #70707075",
  },
  tabRight: {
    borderRight: "2px solid #70707075",
  },
}));

export default function FullWidthTabs(props) {
  const classes = useStyles();
  const [value, setValue] = React.useState(0);
  const { tab, tabInfo } = props;

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  return (
    <div className={classes.root}>
      <ProductTabs variant="fullWidth" value={value} onChange={handleChange}>
        {tab.map((tab, index) => (
          <ProductCustomTab
            disableRipple
            label={tab.name}
            className={index !== 2 ? classes.tabRight : null}
          />
        ))}
      </ProductTabs>
      <>
        {tabInfo &&
          tabInfo.map((product, index) => (
            <TabPanel value={value} index={index}>
              <TabContents productInfo={tabInfo[index]} />
            </TabPanel>
          ))}
      </>
    </div>
  );
}
